'use client';

import React, { useState, useEffect } from 'react';

interface ServicesService {
  id?: number;
  title: string | null;
  description: string | null;
  icon?: string | null;
  key: string | null;
  features: string[];
}

interface ServicesSectionProps {
  language: string;
  showAlert: (message: string, type: 'success' | 'error') => void;
}

const ServicesSection: React.FC<ServicesSectionProps> = ({ language, showAlert }) => {
  const API_URL = 'https://server.ginekoloqayten.online/api';

  const [services, setServices] = useState<ServicesService[]>([]);
  const [showModal, setShowModal] = useState(false);
  const [currentServiceId, setCurrentServiceId] = useState<number | null>(null);
  const [formData, setFormData] = useState<ServicesService>({
    title: null,
    description: null,
    key: null,
    features: [],
  });
  const [iconFile, setIconFile] = useState<File | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    loadServices(language);
  }, [language]);

  const loadServices = async (lang?: string) => {
    try {
      setIsLoading(true);
      const fetchLang = lang || language;
      console.log(`Loading services for language: ${fetchLang}`);
      
      const response = await fetch(`${API_URL}/services?lang=${fetchLang}`);
      if (!response.ok) throw new Error('Failed to load services');
      
      const data = await response.json();
      console.log('Loaded services:', data);
      
      setServices(Array.isArray(data) ? data : []);
      setIsLoading(false);
    } catch (error) {
      console.error('Error loading services:', error);
      setIsLoading(false);
      showAlert('Xidm…ôtl…ôri y√ºkl…ôm…ôk m√ºmk√ºn olmadƒ±', 'error');
    }
  };

  const openServiceModal = (serviceId?: number) => {
    if (serviceId) {
      setCurrentServiceId(serviceId);
      const service = services.find(s => s.id === serviceId);
      if (service) {
        setFormData({
          title: service.title,
          description: service.description,
          icon: service.icon,
          key: service.key,
          features: service.features || [],
        });
      }
    } else {
      setCurrentServiceId(null);
      setFormData({
        title: null,
        description: null,
        key: null,
        features: [],
      });
    }
    setIconFile(null);
    setShowModal(true);
  };

  const closeServiceModal = () => {
    setShowModal(false);
    setCurrentServiceId(null);
    setIconFile(null);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleFeaturesChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const features = e.target.value.split('\n').filter(f => f.trim());
    setFormData(prev => ({ ...prev, features }));
  };

  const handleSaveService = async () => {
    if (!formData.title || formData.title.trim() === '' || !formData.description || formData.description.trim() === '' || !formData.key || formData.key.trim() === '') {
      showAlert('L√ºtf…ôn b√ºt√ºn lazimi sah…ôl…ôri doldurun', 'error');
      return;
    }

    try {
      const formDataToSend = new FormData();
      if (formData.title && formData.title.trim() !== '') {
        formDataToSend.append('title', formData.title.trim());
      }
      if (formData.description && formData.description.trim() !== '') {
        formDataToSend.append('description', formData.description.trim());
      }
      if (formData.key && formData.key.trim() !== '') {
        formDataToSend.append('key', formData.key.trim());
      }
      formDataToSend.append('language_code', language);
      formData.features.forEach((feature, index) => {
        if (feature.trim() !== '') {
          formDataToSend.append(`features[${index}]`, feature.trim());
        }
      });

      if (iconFile) {
        formDataToSend.append('icon', iconFile);
      }

      const url = currentServiceId ? `${API_URL}/services/${currentServiceId}` : `${API_URL}/services`;
      const method = currentServiceId ? 'PUT' : 'POST';

      const response = await fetch(url, {
        method,
        body: formDataToSend,
      });

      if (!response.ok) throw new Error('Failed to save service');

      showAlert(currentServiceId ? 'Xidm…ôt yenil…ôndi!' : 'Xidm…ôt …ôlav…ô edildi!', 'success');
      closeServiceModal();
      
      // Reload services with proper language parameter
      await loadServices(language);
    } catch (error) {
      console.error('Error saving service:', error);
      showAlert('Xidm…ôti yadda saxlamaq m√ºmk√ºn olmadƒ±', 'error');
    }
  };

  const handleDeleteService = async (serviceId: number) => {
    if (!confirm('Bu xidm…ôti silm…ôk ist…ôdiyiniz…ô …ôminsiniz?')) return;

    try {
      const response = await fetch(`${API_URL}/services/${serviceId}`, {
        method: 'DELETE',
      });

      if (!response.ok) throw new Error('Failed to delete service');

      showAlert('Xidm…ôt silindi!', 'success');
      
      // Reload services with proper language parameter
      await loadServices(language);
    } catch (error) {
      console.error('Error deleting service:', error);
      showAlert('Xidm…ôti silm…ôk m√ºmk√ºn olmadƒ±', 'error');
    }
  };

  return (
    <div className="content-section">
      <h2 className="section-title">Xidm…ôtl…ôrin ƒ∞dar…ô Edilm…ôsi</h2>

      <button className="btn btn-primary" onClick={() => openServiceModal()}>
        + Yeni Xidm…ôt ∆èlav…ô Et
      </button>

      <div className="table-container">
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th>Ba≈ülƒ±q</th>
              <th>T…ôsvir</th>
              <th>∆èm…ôliyyatlar</th>
            </tr>
          </thead>
          <tbody>
            {services.map(service => (
              <tr key={service.id}>
                <td>{service.id}</td>
                <td>{service.title}</td>
                <td>{service.description ? service.description.substring(0, 50) : 'N/A'}...</td>
                <td>
                  <div className="action-buttons">
                    <button
                      className="btn btn-primary"
                      onClick={() => openServiceModal(service.id)}
                    >
                      Redakt…ô et
                    </button>
                    <button
                      className="btn btn-danger"
                      onClick={() => service.id && handleDeleteService(service.id)}
                    >
                      Sil
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Service Modal */}
      {showModal && (
        <div className="modal-overlay show">
          <div className="modal-content">
            <button className="close-modal" onClick={closeServiceModal}>&times;</button>
            <h3>{currentServiceId ? 'Xidm…ôti Redakt…ô Et' : 'Xidm…ôt ∆èlav…ô Et'}</h3>

            <div className="form-group">
              <label>Ba≈ülƒ±q</label>
              <input
                type="text"
                name="title"
                value={formData.title || ''}
                onChange={handleInputChange}
                placeholder="Xidm…ôt ba≈ülƒ±ƒüƒ±"
              />
            </div>

            <div className="form-group">
              <label>T…ôsvir</label>
              <textarea
                name="description"
                value={formData.description || ''}
                onChange={handleInputChange}
                placeholder="Xidm…ôtin t…ôsviri"
              />
            </div>

            <div className="form-group">
              <label>ƒ∞kon Faylƒ±</label>
              <input
                type="file"
                accept="image/*"
                onChange={(e) => setIconFile(e.target.files?.[0] || null)}
              />
              <small className="image-hint">PNG, SVG v…ô ya dig…ôr ≈ü…ôkil formatlarƒ± q…ôbul olunur</small>
            </div>

            <div className="form-group">
              <label>A√ßar (t…ôrc√ºm…ôl…ôr √º√ß√ºn)</label>
              <input
                type="text"
                name="key"
                value={formData.key || ''}
                onChange={handleInputChange}
                placeholder="m…ôs., cancerScreening"
              />
            </div>

            <div className="form-group">
              <label>X√ºsusiyy…ôtl…ôr (h…ôr birini yeni s…ôtird…ô …ôlav…ô edin)</label>
              <textarea
                value={formData.features.join('\n')}
                onChange={handleFeaturesChange}
                placeholder="X√ºsusiyy…ôt 1&#10;X√ºsusiyy…ôt 2&#10;X√ºsusiyy…ôt 3"
              />
            </div>

            <div className="btn-group">
              <button className="btn btn-success" onClick={handleSaveService}>
                Xidm…ôti Yadda Saxla
              </button>
              <button className="btn btn-secondary" onClick={closeServiceModal}>
                L…ôƒüv et
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ServicesSection;
