'use client';

import React, { useState, useEffect } from 'react';

interface Blog {
  id?: number;
  title: string | null;
  category: string | null;
  short_description: string | null;
  content: string | null;
  image_url?: string | null;
}

interface BlogsSectionProps {
  language: string;
  showAlert: (message: string, type: 'success' | 'error') => void;
}

const BlogsSection: React.FC<BlogsSectionProps> = ({ language, showAlert }) => {
  const API_URL = 'https://server.ginekoloqayten.online/api';

  const [blogs, setBlogs] = useState<Blog[]>([]);
  const [showModal, setShowModal] = useState(false);
  const [currentBlogId, setCurrentBlogId] = useState<number | null>(null);
  const [currentStep, setCurrentStep] = useState(1);
  const [formData, setFormData] = useState<Blog>({
    title: null,
    category: null,
    short_description: null,
    content: null,
  });
  const [imageFile, setImageFile] = useState<File | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    loadBlogs(language);
  }, [language]);

  const loadBlogs = async (lang?: string) => {
    try {
      setIsLoading(true);
      const fetchLang = lang || language;
      console.log(`Loading blogs for language: ${fetchLang}`);
      
      const response = await fetch(`${API_URL}/blogs?lang=${fetchLang}`);
      if (!response.ok) throw new Error('Failed to load blogs');
      
      const data = await response.json();
      console.log('Loaded blogs:', data);
      
      setBlogs(Array.isArray(data) ? data : []);
      setIsLoading(false);
    } catch (error) {
      console.error('Error loading blogs:', error);
      setIsLoading(false);
      showAlert('Bloglarƒ± y√ºkl…ôm…ôk m√ºmk√ºn olmadƒ±', 'error');
    }
  };

  const openBlogModal = (blogId?: number) => {
    if (blogId) {
      setCurrentBlogId(blogId);
      const blog = blogs.find(b => b.id === blogId);
      if (blog) {
        setFormData({
          title: blog.title,
          category: blog.category,
          short_description: blog.short_description,
          content: blog.content,
          image_url: blog.image_url,
        });
      }
    } else {
      setCurrentBlogId(null);
      setFormData({
        title: null,
        category: null,
        short_description: null,
        content: null,
      });
    }
    setImageFile(null);
    setCurrentStep(1);
    setShowModal(true);
  };

  const closeBlogModal = () => {
    setShowModal(false);
    setCurrentBlogId(null);
    setCurrentStep(1);
    setImageFile(null);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleNextStep = () => {
    if (!formData.title || formData.title.trim() === '' || !formData.category || formData.category.trim() === '' || !formData.short_description || formData.short_description.trim() === '') {
      showAlert('L√ºtf…ôn b√ºt√ºn lazimi sah…ôl…ôri doldurun', 'error');
      return;
    }
    setCurrentStep(2);
  };

  const handlePrevStep = () => {
    setCurrentStep(1);
  };

  const handleSaveBlog = async () => {
    if (!formData.title || formData.title.trim() === '' || !formData.category || formData.category.trim() === '' || !formData.short_description || formData.short_description.trim() === '') {
      showAlert('L√ºtf…ôn b√ºt√ºn lazimi sah…ôl…ôri doldurun', 'error');
      return;
    }

    try {
      const formDataToSend = new FormData();
      if (formData.title && formData.title.trim() !== '') {
        formDataToSend.append('title', formData.title.trim());
      }
      if (formData.category && formData.category.trim() !== '') {
        formDataToSend.append('category', formData.category.trim());
      }
      if (formData.short_description && formData.short_description.trim() !== '') {
        formDataToSend.append('short_description', formData.short_description.trim());
      }
      if (formData.content && formData.content.trim() !== '') {
        formDataToSend.append('content', formData.content.trim());
      }
      formDataToSend.append('language_code', language);

      if (imageFile) {
        formDataToSend.append('image', imageFile);
      }

      const url = currentBlogId ? `${API_URL}/blogs/${currentBlogId}` : `${API_URL}/blogs`;
      const method = currentBlogId ? 'PUT' : 'POST';

      const response = await fetch(url, {
        method,
        body: formDataToSend,
      });

      if (!response.ok) throw new Error('Failed to save blog');

      showAlert(currentBlogId ? 'Blog yenil…ôndi!' : 'Blog …ôlav…ô edildi!', 'success');
      closeBlogModal();
      
      // Reload blogs with proper language parameter
      await loadBlogs(language);
    } catch (error) {
      console.error('Error saving blog:', error);
      showAlert('Blogu yadda saxlamaq m√ºmk√ºn olmadƒ±', 'error');
    }
  };

  const handleDeleteBlog = async (blogId: number) => {
    if (!confirm('Bu blogu silm…ôk ist…ôdiyiniz…ô …ôminsiniz?')) return;

    try {
      const response = await fetch(`${API_URL}/blogs/${blogId}`, {
        method: 'DELETE',
      });

      if (!response.ok) throw new Error('Failed to delete blog');

      showAlert('Blog silindi!', 'success');
      
      // Reload blogs with proper language parameter
      await loadBlogs(language);
    } catch (error) {
      console.error('Error deleting blog:', error);
      showAlert('Blogu silm…ôk m√ºmk√ºn olmadƒ±', 'error');
    }
  };

  return (
    <div className="content-section">
      <h2 className="section-title">Bloglarƒ±n ƒ∞dar…ô Edilm…ôsi</h2>

      <button className="btn btn-primary" onClick={() => openBlogModal()}>
        + Yeni Blog ∆èlav…ô Et
      </button>

      <div className="table-container">
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th>Ba≈ülƒ±q</th>
              <th>Kateqoriya</th>
              <th>T…ôsvir</th>
              <th>∆èm…ôliyyat</th>
            </tr>
          </thead>
          <tbody>
            {blogs.map(blog => (
              <tr key={blog.id}>
                <td>{blog.id}</td>
                <td>{blog.title}</td>
                <td>{blog.category}</td>
                <td>{blog.short_description ? blog.short_description.substring(0, 50) : 'N/A'}...</td>
                <td>
                  <div className="action-buttons">
                    <button
                      className="btn btn-primary"
                      onClick={() => openBlogModal(blog.id)}
                    >
                      Redakt…ô et
                    </button>
                    <button
                      className="btn btn-danger"
                      onClick={() => blog.id && handleDeleteBlog(blog.id)}
                    >
                      Sil
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Blog Modal */}
      {showModal && (
        <div className="modal-overlay show">
          <div className="modal-content">
            <button className="close-modal" onClick={closeBlogModal}>&times;</button>
            <h3>{currentBlogId ? 'Blogu Redakt…ô et' : 'Blog ∆èlav…ô Et'}</h3>

            <div className="step-indicator">
              Addƒ±m {currentStep}: {currentStep === 1 ? '√úmumi M…ôlumat' : 'M…ôzmun Detallarƒ±'}
            </div>

            {currentStep === 1 && (
              <>
                <div className="form-group">
                  <label>Ba≈ülƒ±q</label>
                  <input
                    type="text"
                    name="title"
                    value={formData.title || ''}
                    onChange={handleInputChange}
                    placeholder="Blog ba≈ülƒ±ƒüƒ±"
                  />
                </div>

                <div className="form-group">
                  <label>Kateqoriya</label>
                  <select
                    name="category"
                    value={formData.category || ''}
                    onChange={handleInputChange}
                  >
                    <option value="">Kateqoriya se√ßin</option>
                    <option value="Health">Saƒülamlƒ±q</option>
                    <option value="Wellness">Wellness</option>
                    <option value="Parenting">Valideynlik</option>
                    <option value="Medical">Tibbi</option>
                    <option value="Lifestyle">H…ôyat T…ôrzi</option>
                  </select>
                </div>

                <div className="form-group">
                  <label>Qƒ±sa T…ôsvir</label>
                  <textarea
                    name="short_description"
                    value={formData.short_description || ''}
                    onChange={handleInputChange}
                    placeholder="Blog haqqƒ±nda qƒ±sa m…ôlumat"
                    rows={3}
                  />
                </div>

                <div className="form-group">
                  <label>≈û…ôkil Y√ºkl…ô</label>
                  <input
                    type="file"
                    accept="image/*"
                    onChange={(e) => setImageFile(e.target.files?.[0] || null)}
                  />
                  {formData.image_url && (
                    <p className="image-hint">Cari ≈ü…ôkil: {formData.image_url}</p>
                  )}
                </div>
              </>
            )}

            {currentStep === 2 && (
              <>
                <div className="form-group">
                  <label>M…ôzmun (∆ètraflƒ±)</label>
                  <textarea
                    name="content"
                    value={formData.content || ''}
                    onChange={handleInputChange}
                    placeholder="Blog m…ôzmununu yazƒ±n. Paragraflarƒ± bo≈ü s…ôtirl…ô ayrƒ±n."
                    rows={6}
                  />
                </div>
              </>
            )}

            <div className="btn-group">
              {currentStep === 2 && (
                <button className="btn btn-secondary" onClick={handlePrevStep}>
                  Geri
                </button>
              )}
              {currentStep === 1 && (
                <button className="btn btn-primary" onClick={handleNextStep}>
                  Sonraki
                </button>
              )}
              {currentStep === 2 && (
                <button className="btn btn-success" onClick={handleSaveBlog}>
                  Blogu Yadda Saxla
                </button>
              )}
              <button className="btn btn-secondary" onClick={closeBlogModal}>
                L…ôƒüv et
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default BlogsSection;
