'use client';

import React from 'react';

interface ExportSectionProps {
  language: string;
  showAlert: (message: string, type: 'success' | 'error') => void;
}

const ExportSection: React.FC<ExportSectionProps> = ({ language, showAlert }) => {
  const API_URL = 'https://server.ginekoloqayten.online/api';

  const handleExportData = async () => {
    try {
      const response = await fetch(`${API_URL}/export?lang=${language}`);
      if (!response.ok) throw new Error('Failed to export data');

      const data = await response.json();
      const dataStr = JSON.stringify(data, null, 2);
      const dataBlob = new Blob([dataStr], { type: 'application/json' });
      const url = URL.createObjectURL(dataBlob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `doctor-website-data-${language}-${new Date().toISOString().split('T')[0]}.json`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);

      showAlert(`M…ôlumatlar ${language.toUpperCase()} dild…ô uƒüurla ixrac edildi!`, 'success');
    } catch (error) {
      console.error('Error exporting data:', error);
      showAlert('M…ôlumatlarƒ± ixrac etm…ôk m√ºmk√ºn olmadƒ±', 'error');
    }
  };

  return (
    <div className="content-section">
      <h2 className="section-title">M…ôlumatlarƒ± ƒ∞xrac Et</h2>

      <p style={{ marginBottom: '20px', color: '#7f8c8d' }}>
        B√ºt√ºn m…ôzmununuzu ehtiyat n√ºsx…ô v…ô ya miqrasiya √º√ß√ºn JSON faylƒ± olaraq y√ºkl…ôyin.
      </p>

      <button className="btn btn-primary" onClick={handleExportData}>
        B√ºt√ºn M…ôlumatlarƒ± Y√ºkl…ô (JSON)
      </button>

      <div style={{ marginTop: '30px', padding: '15px', background: '#ecf0f1', borderRadius: '4px' }}>
        <h4 style={{ color: '#2c3e50', marginBottom: '10px' }}>M√∂vcud Endpoint-l…ôr:</h4>
        <p style={{ color: '#7f8c8d', fontSize: '13px' }}>
          <strong>GET /api/hero</strong> - Hero b√∂lm…ôsinin m…ôlumatlarƒ±nƒ± …ôld…ô edin<br />
          <strong>GET /api/about</strong> - Haqqƒ±mƒ±zda b√∂lm…ôsinin m…ôlumatlarƒ±nƒ± …ôld…ô edin<br />
          <strong>GET /api/services</strong> - B√ºt√ºn xidm…ôtl…ôri …ôld…ô edin<br />
          <strong>GET /api/appointment</strong> - G√∂r√º≈ü m…ôlumatlarƒ±nƒ± …ôld…ô edin<br />
          <strong>GET /api/contact</strong> - ∆èlaq…ô m…ôlumatlarƒ±nƒ± …ôld…ô edin<br />
          <strong>GET /api/feedbacks</strong> - B√ºt√ºn r…ôyl…ôri …ôld…ô edin<br />
          <strong>GET /api/blogs</strong> - B√ºt√ºn bloglarƒ± …ôld…ô edin<br />
          <strong>GET /api/export</strong> - B√ºt√ºn m…ôlumatlarƒ± ixrac edin<br />
        </p>
      </div>

      <div style={{ marginTop: '30px', padding: '15px', background: '#d1ecf1', borderRadius: '4px', border: '1px solid #bee5eb' }}>
        <h4 style={{ color: '#0c5460', marginBottom: '10px' }}>API ∆èsas √únvan:</h4>
        <p style={{ color: '#0c5460', fontSize: '13px', fontFamily: 'monospace' }}>
          https://server.ginekoloqayten.online/api
        </p>
      </div>

      <div style={{ marginTop: '30px', padding: '15px', background: '#fff3cd', borderRadius: '4px', border: '1px solid #ffeaa7' }}>
        <h4 style={{ color: '#856404', marginBottom: '10px' }}>Qeyd:</h4>
        <p style={{ color: '#856404', fontSize: '13px' }}>
          B√ºt√ºn API sorƒüularƒ± multipart/form-data v…ô ya application/json √ºst√ºns√ºz q…ôbul edir. 
          ≈û…ôkil y√ºkl…ôm…ô zamanƒ± formData istifad…ô edin.
        </p>
      </div>
    </div>
  );
};

export default ExportSection;
